
.home-header {
  background-color: hsl(0, 9%, 90%);
  padding-bottom: 2rem;
}


.home-header-container {
  display: flex;
  gap: 5rem;
  width: 80%;
  margin: 2rem auto;
}

.home-text {
   & h1 {
    font-size: 3rem;
    font-weight: 700;
    margin-bottom: 1rem;
   }

   & h2{
    font-size: 2.5rem;
    font-weight: 300;
    margin-bottom: 1rem;
   }
}

.home-profile-img {
  width: 400px;
  height: 400px;
  border-radius: 50%;
  margin: 0 auto;
  display: block;
}

.primary-button {
  background-color: white;
  padding: 1rem 2rem;
  border-bottom: 5px solid hsl(0, 0%, 86%);
  border-right: 5px solid hsl(0, 0%, 86%);
  width: fit-content;
  margin-top: 2rem;;
  border-radius: 5px;
  font-size: 1.2rem;
  cursor: pointer;
}

.services-container {
  display: flex;
  gap: 2rem;
  width: 80%;
  margin: 0 auto;
  padding: 2rem 0;
}

.service a {
  color: #333;
}

.clients {
  width: 75%;
  margin: 2rem auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;

}

.clients-container {
  display: flex;
  justify-content: space-between;
}

.client-logo {
  width: 300px;
  height: 100px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  width: 80%;
  margin: 0 auto;
}

.navbar-logo {
  font-size: 1.5rem;
  font-weight: 700;
}

.navbar-list {
  display: flex;
  gap: 2rem;
  list-style: none;
}

.footer {
  background-color: hsl(0, 9%, 90%);
  margin-top: 4rem;
  padding: 2rem 0;
  
}

.footer-container {
  display: flex;
  gap: 2rem;
  width: 80%;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
}

.footer-socials {
  display: flex;
  gap: 2rem;
  align-items: center;

  & a {
    color: #333;
  }
}

.socials-logo {
  width: 100px;
}